<template>
   <div class="int-admin" :class="{'int-admin-wide': isOpen == false, 'int-admin-normal': isOpen == true }">
      <sidebar :class="{'sidebar-close': isOpen == false, 'sidebar-open': isOpen == true }" />
      <div class="int-admin-page--wrap">
         <nav class="navbar admin-navbar p_layout navbar-expand-lg main-navbar">

            <button class="navbar-toggler mr-3" type="button" data-bs-toggle="collapse" data-bs-target="#navCollapse" aria-controls="navCollapse" aria-expanded="false" aria-label="Toggle navigation">

            </button>

            <div class="collapse navbar-collapse" id="navCollapse">

               <ul class="navbar-nav justify-self-start">
                  <button id="toggleSidebar" class="btn toggle-sidebar" @click="isOpen = !isOpen">
                     <template v-if="isOpen == true">
                        <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000">
                           <path d="M0 0h24v24H0z" fill="none" />
                           <path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z" />
                        </svg>
                        <b-tooltip target="toggleSidebar" class="text-left" placement="right" triggers="hover">
                           Click
                           <strong>crtl</strong> +
                           <strong>h</strong> to hide the Sidebar
                        </b-tooltip>
                     </template>
                     <template v-if="isOpen == false">
                        <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#000000">
                           <path d="M0 0h24v24H0z" fill="none" />
                           <path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6z" />
                        </svg>
                        <b-tooltip target="toggleSidebar" class="text-left" placement="right" triggers="hover">
                           Click
                           <strong>crtl</strong> +
                           <strong>d</strong> to Display the Sidebar
                        </b-tooltip>
                     </template>
                  </button>
                  <li class="nav-item hover-item rounded">
                     <router-link class="nav-link router-link-exact-active router-link-active" to="/" exact>Dashboard</router-link>
                  </li>
                  <li class="nav-item hover-item rounded">
                     <a class="nav-link" to="/trips">
                        Reports
                     </a>
                  </li>
                  <li class="nav-item hover-item rounded">
                     <a class="nav-link" to="/support">
                        Support
                     </a>
                  </li>
               </ul>
               <router-link id="v-step-1" class="btn main-btn rounded outline-btn ml-3" to="/rout-track">Rout track</router-link>
               <router-link id="v-step-2" class="btn main-btn rounded gold-btn ml-3" to="/calculate-price">Calculate price</router-link>
               <!-- <button @click="swtchLocale('en')" class="btn main-btn rounded gold-btn ml-3">en</button>
               <button @click="swtchLocale('de')" class="btn main-btn rounded gold-btn ml-3">de</button> -->

               <div class="d-flex flex-lg-row flex-md-column col-md-reverse ml-auto">
                  <div class="form-group mr-3 mb-0">
                     <select class="form-control" v-model="langs" id="exampleFormControlSelect1" @change="swtchLocale(langs)">
                        <option value="en">en</option>
                        <option value="de">de</option>
                     </select>
                  </div>
                  <div class="dropdown">
                     <button class="btn btn-secondary dropdown-toggle p-0 bg-transparent border-none" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
                        <div class="profile-pic sm-pic h5  p-2 px-3 rounded text-dark-heading mb-0">
                           Ahmed fared
                        </div>
                     </button>
                     <ul class="dropdown-menu dropdown-menu-end" aria-labelledby="dropdownMenuButton1">
                        <li>
                           <a class="dropdown-item">Profile Settings</a>
                        </li>
                        <li>
                           <hr class="dropdown-divider">
                        </li>
                        <li>
                           <router-link class="dropdown-item" to="/">log out</router-link>
                        </li>
                     </ul>
                  </div>
               </div>
            </div>

         </nav>
         <div class="int-admin-layout">
            <router-view />
            <router-view name="moreInfos" class="more-infos-modal" />
            <div class="dropdown">
               <a class="btn quk-actions bg-gold dropdown-toggle mt-3" role="button" id="dropdownactions" data-bs-toggle="dropdown" aria-expanded="false" v-b-tooltip.hover.left title="Quik actions">
                  <svg id="_03_Login" data-name="03 Login" xmlns="http://www.w3.org/2000/svg" width="25.741" height="25.741" viewBox="0 0 25.741 25.741">
                     <path id="Path_179" data-name="Path 179" d="M232.257,25.741A1.257,1.257,0,0,1,231,24.484V1.257a1.257,1.257,0,1,1,2.514,0V24.484A1.257,1.257,0,0,1,232.257,25.741Z" transform="translate(-219.386)" fill="#fff"></path>
                     <path id="Path_180" data-name="Path 180" d="M24.484,233.514H1.257a1.257,1.257,0,1,1,0-2.514H24.484a1.257,1.257,0,1,1,0,2.514Z" transform="translate(0 -219.386)" fill="#fff"></path>
                  </svg>
               </a>

               <ul class="dropdown-menu box-shadow-large border-none px-4 py-3" aria-labelledby="dropdownactions">
                  <li>
                     <router-link to="/vehicles/add" class="dropdown-item h5 border-bottom py-2 px-0">Add vehicle</router-link>
                  </li>
                  <li>
                     <a class="dropdown-item h5 border-bottom py-2 px-0">Create Invoice</a>
                  </li>
                  <li>
                     <router-link to="/calculate-price" class="dropdown-item h5 py-2 px-0">Calculate Price</router-link>
                  </li>
               </ul>
            </div>
         </div>
      </div>
      <v-tour name="myTour" :steps="steps"></v-tour>
   </div>
</template>

<script>
import sidebar from "./components/global/Sidebar.vue";
// import AddBtn from "@/components/buttons/AddBtn";

export default {
	name: "module",
	components: {
		sidebar
		// AddBtn
	},
	data() {
		return {
			langs: "",
			steps: [
				{
					target: "#v-step-0", // We're using document.querySelector() under the hood
					content: `
                  <p class="f4">Press <span class="btn-key">ctrl</span> + <span class="btn-key">h</span> to hide the sidebar</p>
                  <p class="f4">Press <span class="btn-key">ctrl</span> + <span class="btn-key">d</span> to display the sidebar</p>
         
               `,
					params: {
						placement: "right" // Any valid Popper.js placement. See https://popper.js.org/popper-documentation.html#Popper.placements
					}
				},
				{
					target: "#v-step-1",
					content: "Live tracking for trips"
				},
				{
					target: "#v-step-2",
					content: "Calculate Price"
				},
				{
					target: "#dropdownactions",
					content: "control you actions with Quik actions"
				}
			]
		};
	},

	computed: {
		isOpen: {
			get() {
				return this.$store.state.adminPanel.isOpen;
			},
			set(value) {
				this.$store.commit("isOpen", value);
			}
		}
	},

	methods: {
		openSidebar() {
			this.isOpen = true;
		},
		closeSidebar() {
			this.isOpen = false;
		},
		swtchLocale(locale) {
			this.$i18n.locale = locale;
			console.log(locale);
			localStorage.setItem("locale", locale);
		}
	},
	mounted() {
		this._keyListener = function(e) {
			if (e.key === "d" && (e.ctrlKey || e.metaKey)) {
				e.preventDefault();
				this.openSidebar();
			}
			if (e.key === "h" && (e.ctrlKey || e.metaKey)) {
				e.preventDefault();
				this.closeSidebar();
			}
		};

		document.addEventListener("keydown", this._keyListener.bind(this));

		this.$tours["myTour"].start();
	}
};
</script>

<style lang="scss">
.scale-slide-enter-active,
.scale-slide-leave-active {
	position: absolute;
	transition: all 0.85s ease;
}

.scale-slide-enter-from {
	left: -100%;
}

.scale-slide-enter-to {
	left: 0%;
}

.scale-slide-leave-from {
	transform: scale(1);
}

.scale-slide-leave-to {
	transform: scale(0.8);
}
</style>


